<template>
  <div class="shop-cart">
    <div class="content">
      <div class="content-left"
           @click="isfood()">
        <div class="logo-wrapper">
          <div class="logo"
               :class="{'highlight':totalPrice>0}">
            <i class="icon-shopping_cart"></i>
          </div>
          <div class="num"
               v-show="num">{{num}}</div>
        </div>
        <div class="price"
             :class="{'highlight':totalPrice>0}">¥{{totalPrice}}元</div>
        <div class="desc">另需配送费¥{{deliveryPrice}}元</div>
      </div>
      <div class="content-right">
        <div class="pay"
             :class="{'highlight':totalPrice>=minPrice}">
          {{payDesc}}
        </div>
      </div>
    </div>
    <shopCartList v-show="listShow"
                  ref="shopCartList"
                  :selectFoods='selectFoods'
                  :isfood='food' />
  </div>
</template>

<script>
import cartcontrol from '@/components/cartcontrol/cartcontrol'
import shopCartList from '@/components/shopCartList/shopCartList'


export default {

  data () {
    return {
      food: {
        is: false
      }
    }
  },
  props: {
    deliveryPrice: {
      type: Number,
      default: 0
    },
    minPrice: {
      type: Number,
      default: 0

    },
    selectFoods: {
      type: Array,
      default () {
        return []
      }
    }
  },
  methods: {
    isfood () {
      if (this.num > 0) {
        this.food.is = !this.food.is
      }
      this.$refs.shopCartList.scoll()
    }
  },
  computed: {
    totalPrice () {
      let total = 0;
      for (let item of this.selectFoods) {
        total += item.price * item.count;
      }
      return total;
    },
    num () {
      let count = 0;
      for (let item of this.selectFoods) {
        count += item.count;
      }
      return count;
    },
    payDesc () {
      if (this.totalPrice === 0) {
        return `¥${this.minPrice}元起送`
      } else if (this.totalPrice < this.minPrice) {
        return `还差¥${this.minPrice - this.totalPrice}起送`
      } else {
        return "去结算"
      }
    },
    listShow () {
      if (this.num == 0) {
        this.food.is = false
        return this.food.is
      } else if (this.food.is) {
        return true
      }
    }
  },
  components: {
    cartcontrol,
    shopCartList
  }

}
</script>

<style lang="stylus">
.shop-cart
  position fixed
  bottom 0px
  width 100%
  height 48px
  z-index 25
  .content
    display flex
    background #141d27
    color rgba(255, 255, 255, 0.4)
    .content-left
      flex 1
      .logo-wrapper
        display inline-block
        position relative
        background #141d27
        top -10px
        margin 0 12px
        padding 6px
        width 56px
        height 56px
        box-sizing border-box
        vertical-align top
        border-radius 50%
        font-size 34px
        color #fff
        .logo
          width 100%
          height 100%
          border-radius 50%
          background #2b343c
          text-align center
          color #80858a
          &.highlight
            background rgb(0, 160, 220)
            color #fff
          .icon-shopping_cart
            line-height 44px
            font-size 24px
        .num
          position absolute
          top 0
          right 0
          width 24px
          height 16px
          line-height 18px
          text-align center
          border-radius 16px
          font-size 9px
          font-weight 700
          color #fff
          background rgb(240, 20, 20)
          box-shadow 0 4px 8px 0 rgba(0, 0, 0, 0.4)
      .price
        display inline-block
        vertical-align top
        line-height 24px
        margin-top 12px
        box-sizing border-box
        padding-right 12px
        border-right 1px solid rgba(255, 255, 255, 0.1)
        font-size 16px
        font-weight 700
        &.highlight
          color #fff
      .desc
        display inline-block
        vertical-align top
        margin 12px 0 0 12px
        line-height 24px
        font-size 10px
    .content-right
      flex 0 0 105px
      width 105px
      .pay
        height 48px
        line-height 48px
        text-align center
        font-size 12px
        font-weight 700
        background #2b333b
        &.highlight
          background #00b43c
          color #fff
          font-size 14px
</style>